.buttons {
  display: flex;
  flex-wrap: wrap;
}

.buttons__button {
  background-color: transparent;
  font-size: 16px;
  color: teal;
  border: none;
  transition: color 0.5s;
  margin-left: 20px;
}

@media (max-width: 767px) {
  .buttons__button {
    flex-basis: 100%;
    margin: 10px;
  }
}

.buttons__button:hover {
  color: hsl(191, 54%, 45%);
}

.buttons__button:active {
  color: hsl(180, 100%, 35%);
}

.buttons__button:disabled {
  color: #ccc;
}
